import React, { useState } from 'react';
import FormSell from './FormSell';
import FormSuccess from './FormSuccess';
import {
  FormContainer,
  CloseButton,
  FormContentLeft,
  FormImage,
  FormComponentContainer

} from './FormElements'
import Market from '../../images/market.jpg';

const FormSellHelper = () => {
  const [sellSubmit, sellSubmitted] = useState(false);

  function submitForm() {
    sellSubmitted(true);
  }
  
  return (
    <>
    <FormComponentContainer>
      <FormContainer>
        <CloseButton>×</CloseButton>
        <FormContentLeft>
          <FormImage src={Market} alt='Market' />
        </FormContentLeft>
        {!sellSubmit ? (
          <FormSell submitForm={submitForm} />
        ) : (
          <FormSuccess />
        )}
      </FormContainer>
      </FormComponentContainer>
    </>
  );
};

export default FormSellHelper;
